<template>
  <div id="singleBlog">
    <p v-show="!flag">网络有点卡！ 强稍等一会</p>
    <h3>{{blog.title}}</h3>
    <p>{{blog.content}}</p>
    <button @click="delteSingleBlog">删除</button>
    <router-link :to="'/edit/'+id">编辑</router-link>
  </div>
</template>

<script>
export default {
  name: "single-blog",
  data() {
    return {
      id: this.$route.params.id,
      blog: {},
      flag: false
    };
  },
  created() {
    this.$http
      .get("https://ddbase-46a26.firebaseio.com/posts/" + this.id + ".json")
      .then(data => {
        return data.json();
      })
      .then(data => {
        this.blog = data;
        this.flag = true;
      });
  },
  methods: {
    delteSingleBlog(id) {
      this.$http
        .delete(
          "https://ddbase-46a26.firebaseio.com/posts/" + this.id + ".json"
        )
        .then(() => this.$router.push({ path: "/" }));
    }
  }
};
</script>

<style scoped>
#singleBlog {
  box-sizing: border-box;
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #ddd;
}
</style>