<template>
  <div id="show-blogs">
    <!-- ShowBlogs -->
    <h1>博客总览</h1>
    <input type="text" placeholder="搜索" v-model="search" />
    <div v-for="blog of filterBlogs" class="single-blog">
      <router-link :to="'/blog/'+blog.id">
        <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
      </router-link>
      <article>{{blog.content | overflow}}</article>
    </div>
  </div>
</template>

<script>
export default {
  name: "ShowBlogs",
  data() {
    return {
      blogs: [],
      search: ""
    };
  },
  created() {
    this.$http
      .get("https://ddbase-46a26.firebaseio.com/posts.json")
      .then(data => {
        return data.json();
      })
      .then(data => {
        // console.log(data);
        let blogArr = [];
        for (let k in data) {
          data[k].id = k;
          blogArr.push(data[k]);
        }
        this.blogs = blogArr;
      });
  },
  computed: {
    filterBlogs() {
      return this.blogs.filter(blog => blog.title.match(this.search));
    }
  },
  directives: {
    rainbow: {
      bind(el, binging) {
        el.style.color = `#${Math.random()
          .toString(16)
          .slice(2, 8)}`;
      }
    }
  }
};
</script>

<style>
#show-blogs {
  max-width: 800px;
  margin: 0 auto;
}
.single-blog {
  box-sizing: border-box;
  background-color: #ccc;
  padding: 20px;
  margin: 10px 0;
  border-radius: 4px;
}
.single-blog > a {
  text-decoration: none;
}
input[type="text"] {
  box-sizing: border-box;
  width: 100%;
  padding: 12px;
  font-size: 16px;
  border-radius: 2px;
  border: 1px solid #ccc;
  outline: none;
}
input[type="text"]:focus {
  border-color: blue;
}
</style>