<template>
  <div id="add-blog">
    <h2>编辑博客</h2>
    <form v-show="!submmit">
      <label for="title">博客标题</label>
      <input type="text" v-model="blog.title" required id="title" />
      <label for="content">博客内容</label>

      <textarea v-model="blog.content" id="content"></textarea>
      <div id="checkBoxes">
        <label for="vue">Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories" id="vue" />
        <label for="react">React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories" id="react" />
        <label for="jquery">jQuery</label>
        <input type="checkbox" value="jQuery" v-model="blog.categories" id="jquery" />
        <label for="node">Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories" id="node" />
      </div>

      <label for>作者</label>
      <select v-model="blog.author">
        <option v-for="author in authors">{{author}}</option>
      </select>

      <button @click.prevent="post">修改博客</button>
    </form>
    <div v-show="submmit">
      <h3>你的博客发布成功</h3>
    </div>
    <hr />

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题:{{blog.title}}</p>
      <p>博客内容:</p>
      <p>{{blog.content}}</p>
      <p>博客分类:</p>
      <ul>
        <li v-for="item of blog.categories">{{item}}</li>
      </ul>
      <p>作者:{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "edit",
  data() {
    return {
      blog: {},
      authors: ["jia", "jun", "da"],
      submmit: false,
      id: this.$route.params.id
    };
  },
  methods: {
    post() {
      this.$http
        .put(
          "https://ddbase-46a26.firebaseio.com/posts/" + this.id + ".json",
          this.blog
        )
        .then(res => {
          console.log(res);
          this.submmit = true;
        });
    }
  },
  created() {
    this.$http
      .get("https://ddbase-46a26.firebaseio.com/posts/" + this.id + ".json")
      .then(data => data.json())
      .then(data => (this.blog = data));
  }
};
</script>

<style scoped>
ol,
ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}
#add-blog {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}
#add-blog * {
  box-sizing: border-box;
}
label {
  display: block;
  margin: 20px 0 10px;
}
input[type="text"],
select,
textarea {
  display: block;
  width: 100%;
  padding: 8px;
}
#checkBoxes label {
  display: inline-block;
  margin-top: 0;
}
#checkBoxes input {
  display: inline-block;
  margin-right: 20px;
}
button {
  display: block;
  margin: 20px 0;
  background-color: crimson;
  color: #ffffff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
}
#preview {
  padding: 15px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}
h3 {
  margin-top: 10px;
}
textarea {
  height: 200px;
  resize: none;
}
select option {
  /* height: 20px; */
  font-size: 22px;
}
</style>
