import Vue from 'vue'
import App from './App.vue'
import VueResource from 'vue-resource'
import VueRouter from 'vue-router';
import Routes from './routes';
Vue.config.productionTip = false
Vue.use(VueResource)
Vue.use(VueRouter)

Vue.directive('width', {
  bind(el, binging) {
    if (binging.value === 'all') {
      el.style.maxWidth = 1260 + 'px';
      el.style.background = '#6677cc';
      el.style.padding = 20 + 'px'
    }
  }
})
Vue.filter('to-uppercase', value => value.toUpperCase());
Vue.filter('overflow', value => value.slice(0, 100) + '...');


// 创建路由 
const router = new VueRouter({
  routes: Routes,
  mode: 'history'
})


new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
